.fab {
  font-family: 'Font Awesome 5 Brands';
  font-weight: 400; }

:root {
    --primary-color: #222;
    --secondary-color-darker: #111;
    --secondary-color-dark: #222;
    --secondary-color-light: #818181;
    --secondary-color-lighter: #bbb;
    --first-accent-color: #7dba5c;
    --first-accent-color-dark: #539149;
    --second-accent-color: #ffca28;
}

button {
    display: inline-block;
    padding: 8px 15px;
    background: #bbb;
    background: var(--secondary-color-lighter);
    color: #222;
    color: var(--secondary-color-dark);
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 600;
    border:0px;

    &:hover {
        cursor: pointer;
        background: #7dba5c;
        background: var(--first-accent-color);
    }

    &:active {
        background: #539149;
        background: var(--first-accent-color-dark);
    }
}

@font-face {
  font-family: "oswald-light";
  src: url(/static/media/Oswald-Light.4be970a1.ttf);
}

@font-face {
  font-family: "oswald-extra-light";
  src: url(/static/media/Oswald-ExtraLight.309040b6.ttf);
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: var(--secondary-color-lighter);
  line-height: 22px;
  overflow-y: hidden;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  background: var(--primary-color);
  margin: 0;
}

h1,
h2,
h3 {
  margin-top: 16px;
  font-family: "Raleway", sans-serif;
  border-bottom: 0px solid #dedede;
  padding-bottom: 3px;
}

